import React ,{useState} from 'react';
import camera from './camera.png';
import Swal from 'sweetalert2';
import Base from './Base';
import { Tag } from './Components';
const Uploadphoto = () =>{
    const [Image, setImage] = useState({
        laoder:true,
        imgArray:[]
    })
    const [Caption, setCaption] = useState({
        photo_caption:""
    })
    
const [Url, setUrl] = useState([])
const [FinalUrl,setFinalUrl]=useState("")
const[PreviewImage,setPreviewImage]=useState({
    loader:true,
    file:[]
})
const [Tags,setTags]=useState([])
    const {photo_caption}=Caption
    const {imgArray}=Image
    const fileObj=[]
    const fileArray=[]
const baseURL='https://dmstaging.dronahq.com/mdlz_russia_photogallery';
     const UploadImageToS3=(formData)=>{
        return fetch("https://plugin.api.dronahq.com/api/Imageupload",{
            method:"POST",
            body:formData
        }).then((resp)=>{
            return resp.json()
        }).catch((error)=>{
            return error;
        })
    }
    
    const uploadAttachment=(imgArr)=>{
        var retArray = [];
        for(var i=0;i<imgArr.length;i++)
        {
            if(imgArr[i]==="string")
            {
                retArray.push(imgArr[i]);
                
            }
            else{
                const formData=new FormData()
                const blobData=imgArr[i];
                if(blobData instanceof Blob)
                {
                    var extension = imgArr[i].name.split('.')[imgArr[i].name.split('.').length - 1];
                    formData.append('blob',blobData,"test."+extension);
                }
                else{
                    formData.append('file',blobData)
                }
                
                formData.append('dhqOperation','dhqPackageImageUpload')
                formData.append('tokenKey',"35a35ad1ccd15ef778cc71542b8f573ab6b01bed4c2389bf38")
                UploadImageToS3(formData).then(resp=>{
                    
                    // setUrl([...Url,resp.image_url)
                    retArray.push(resp.image_url)
                    
                }).catch(error=>{
                    console.log('some error occurred');
                })
            }
        }
       return retArray
    }  
    
    

    const SubmitPhotoUrl=(urlArrayParam)=>{
        return fetch(`${baseURL}/api/InsertPhoto`,{
            method:"POST",
            headers:{
                "Content-Type":"application/json",
                'Authorization':"08e5c57c7418ec24d538d4c8cc587ef11e187a0e82028e20f4"
            },body:JSON.stringify({
                "photoCaption":photo_caption,
                "photoUrls":urlArrayParam[0],
                "submitorEmail":localStorage.getItem('useremail'),
                "likableFlag":1,
                "commentableFlag":1,
                "sharableFlag":1,
                "bookmarkFlag":1
            })
        })
        
        }

    const onclick=(event)=>{
        const a=event.target.files;
        setImage({...Image,imgArray:a})
        
        let imgUrls = uploadAttachment(a);
        
        setFinalUrl(imgUrls)
        
        fileObj.push(a)
        for(let i=0;i<fileObj[0].length;i++)
        {
            fileArray.push(URL.createObjectURL(fileObj[0][i]))
        }
        setPreviewImage({...PreviewImage,file:fileArray})
    }

    const onCaption=(event)=>{
        setCaption({...Caption,photo_caption:event.target.value})
    }

    
//    const onTag=(e)=>{
//        const data=e.target.value
//        if(Tags.length<=3)
//        {
//         setTags([...Tags,data])
//        } 
//    }
      
//    const onTagDelete=(e)=>{
//     var arr=[...Tags]
//     var index = arr.indexOf(e.target.value)
//     if (index !== -1) {
//         arr.splice(index, 1);
//         setTags(arr)
//     }
//    }

    const UploadPhotoToGallery=()=>{
        if(Caption.photo_caption==="")
        {
            Swal.fire('Caption is required')
        }
        else
        {
            SubmitPhotoUrl(FinalUrl)
            
            setCaption({...Caption,photo_caption:""})
            Swal.fire({
                icon: 'success',
                title: 'Your work has been saved',
                showConfirmButton: true,
                
            }).then((result)=>{
                return result.isConfirmed? window.history.go('/'):""
                
            })
        }
    }
    
    
    
    const Upload =()=>{
        return(

           
        
        <Base title="Upload Photo" funNav="/" disp="none" >
             {/* This is Image body */}
             <div className='imgdiv'>
                {PreviewImage.file==""? <img src={camera} className='cover'></img>:PreviewImage.file.map((url)=>{
                   return <img src={url} className='cover'></img>
                })}
                 <div className='input-div' >
                <input placeholder="Write about the photo**" value={Caption.photo_caption} type='text' className='input-box' onChange={onCaption} required></input>
                
                </div>
                {/* <div className="tag-div">
                    
                    {Tags!=""?Tags.map((item)=>{
                      return  <button className="badge badge-success" style={{"font-size":"13px","margin-left":"1px"}} value={item} onClick={onTagDelete}>{item} </button>
                    }):<h6 style={{"opacity":"0.6"}}>Please add Tag</h6>}
                    <hr className="line"></hr>
                </div> */}
                {/* <Tag tag={onTag}></Tag> */}
            </div>

            {/* Input boxes */}

           

            

         {/* Footer Button  */}
         {imgArray!=""? 
             <label className='btnClass footerDiv flex-center' onClick={UploadPhotoToGallery}>
                Upload Photo
             </label>: <div className=' footerDiv flex-center'>
                        <label for="myFile"  className="btnClass flex-center" >Add Photo</label>
                        <input  type="file" id="myFile" name="myFile" accept="image/x-png,image/gif,image/jpeg" style={{display:"none"}}  onChange={onclick} multiple/>
                    </div>}
           
        </Base>
       
    
        )
    }

    return(
        <div >
            {Upload()}
        
        </div>
       
    )
}
export default Uploadphoto;