import React from 'react';

import {useParams} from 'react-router-dom'
// import {DronaHQ} from '../dronahq'
const baseURL='https://dmstaging.dronahq.com/mdlz_russia_photogallery';

export const GetPhotoFromAPI=()=>{
    return fetch(`${baseURL}/api/GetPhotoGalleryList`,{
        method:"GET",
        headers:{
            "Content-Type":"application/json",
            "Authorization":"08e5c57c7418ec24d538d4c8cc587ef11e187a0e82028e20f4"
        }
    }).then(response =>  {
      return  response.json();

    })
    .catch(error => {
        console.log(error)
    })
}

export const SetComment=(CommentValue,photoId)=>{
  return fetch(`${baseURL}/api/SetComment`,{
      method:"POST",
      headers:{
          "Content-Type":"application/json",
          "Authorization":"08e5c57c7418ec24d538d4c8cc587ef11e187a0e82028e20f4",
          
      },body:JSON.stringify({
        "userEmail":localStorage.getItem('useremail'),
        "photoId":photoId,
        "comments":CommentValue
      })
  }).then(response=>{
    return response.json()
  }).catch(error=>{
    console.log(error);
  })
}










