export const GetAllComments=(photoId)=>{
  return fetch(`${baseURL}/api/GetAllcomments`,{
    method:"POST",
    headers:{
      "Content-Type":"application/json",
      "Authorization": "08e5c57c7418ec24d538d4c8cc587ef11e187a0e82028e20f4"
    },body:JSON.stringify({
      "photoId":photoId
    })
  }).then(response=>{
    return response.json()
  }).catch(err=>{console.log(err)})
}
  
export const GetAllPhotoBasedOnFilter=(filterValue)=>{
  return fetch(`${baseURL}/api/GetPhotoGalleryListFiltered`,{
    method:"POST",
        headers:{
            "Content-Type":"application/json",
            "Authorization":"08e5c57c7418ec24d538d4c8cc587ef11e187a0e82028e20f4"
        },body:JSON.stringify({
          "filterFlag":filterValue
        })
  }).then(response=>{
    return response.json()
  }).catch(error=>{
    console.log(error);
  })
}

export const SetLike=(LikeDislikeValue,photoid)=>{
  return fetch(`${baseURL}/api/SetPhotoLike`,{
      method:'POST',
      headers:{
          "Content-Type":"application/json",
          "Authorization":"08e5c57c7418ec24d538d4c8cc587ef11e187a0e82028e20f4",
      },body:JSON.stringify({
        "userEmail":localStorage.getItem('useremail'),
        "photoId":photoid,
        "likeOrDislikeFlag":LikeDislikeValue
      })
      
  })
}


export const GetUserDetails=()=>{
  return fetch(`${baseURL}/api/GetUserAuthentication`,{
    method:"POST",
    headers:{
      "Content-type":"application/json",
      "Authorization":"08e5c57c7418ec24d538d4c8cc587ef11e187a0e82028e20f4"
    },body:JSON.stringify({
      "user_email":localStorage.getItem('useremail')
    })
  }).then(response=>{
    return response.json()
  }).catch(error=>{
    console.log(error);
  })
}
