import React from 'react';
import {Link} from 'react-router-dom'
import {Filter} from './Components'
const testNav=()=>{
    alert("hello")
}
const testFilter=()=>{
    console.log("ok")
}

const Base = ({
    title="Photo Upload",
    funNav='/',
    funFilter={testFilter},
    children,
    disp

})=>{
    return(
        <div>
             <div className="d-flex flex-row justify-content text-light box-shadow base">
                <div className="p-2 bd-highlight color-head" style={{fontSize:"20px"}}>
                {funNav==='exit'?<i className="fa fa-angle-left color-head" onClick={testFilter}></i>:<Link to={funNav}>
                    <i className="fa fa-angle-left color-head"></i>
                </Link>}
                </div>
                <div className="p-2 bd-highlight color-head" style={{fontSize:"20px"}}>{title}</div>
                <div className="ml-auto p-2 bd-highlight color-head ">
                    <Filter display={disp} click={funFilter}></Filter>
                </div>
            </div>
                <div>
                    {children}
                </div>
        </div>
    )
}

export default Base;
