import React, { useState, useEffect } from 'react';
import {useParams} from 'react-router-dom'
import Base from './Base';
import {GetAllComments,SetLike,GetPhotoFromAPI} from './API/Api'
import {CommentCard} from './Card';
export const CommentBox=(props)=>{
    
    return(
        <div className='d-flex bd-highlight'>
             <div className="p-2 flex-grow-1 bd-highlight">
            <input type="text" value={props.val} className='input-box' onChange={props.change}  placeholder='Comment here..'/>
             </div>
             <div className="p-2 bd-highlight">
           {props.val!=""? <i className="far fa-paper-plane icon-size" 
            onClick={props.send}
            style={{"padding-top":"17px","padding-right":"5px" }}  ></i>: <i className="far fa-paper-plane icon-size" 
            
            style={{"padding-top":"17px","padding-right":"5px","opacity":"0.5" }} disabled ></i>}

             </div>
        </div>
    )
}



export const Like=(props)=>{
    const [LikedValue,setLikedValue]= useState(props.like)
    const [New,setNew]=useState(0)

    useEffect(()=>{
        updateLike()
    },[])
    
    const onLike=(e)=>{
        SetLike(1,props.photoId)
        setLikedValue(LikedValue+1)
        setNew(1)
    }
    
    const onUnike=(e)=>{  
        SetLike(0,props.photoId)
        setLikedValue(LikedValue-1)
        setNew(0)
    }

    const updateLike=()=>{
        
        if(props.likedByUsers!==null)
        {
            props.likedByUsers.map((item)=>{
                if(item.liked_by_user_email===localStorage.getItem('useremail'))
                {
                    setNew(1)
                }
            })
        }
        
       
    }

    

    

    return(
        <>
            
            <div className="like-div">           
               {New===1?<i className="fas fa-heart icon-size" value="OnFoundUnlike" onClick={onUnike}></i>:
                <i className="far fa-heart icon-size" value="OnNullLike" onClick={onLike}></i>}
                <div className="like">{LikedValue}</div>    
            </div>
            
        </>
    )
}

export const ViewComments=()=>{

    const [Comments, setComments] = useState({
        loader:true,
        content:[]
    })
    useEffect(() => {
        ViewAllComments()
    },[])
    const ncomments=(val)=>{
        return(
            <>
            <CommentCard
            profile={val.commented_by_user_img_url}
            name={val.commented_by_user_name}
            comments={val.comments}
            ></CommentCard>
            
            </>
            
        )
    }
    const {photoId} = useParams()
    console.log(photoId);
    const ViewAllComments= async()=>{
        
        const data=await GetAllComments(photoId)
        console.log(data);
        setComments({...Comments,content:data,loader:false})
    }
    
    return(
        <>
            <Base title="Comments" funNav='/'>
                {Comments.loader==true?<img src="https://media.giphy.com/media/3oEjI6SIIHBdRxXI40/giphy.gif" className="loader"/>:
                Comments.content.commentedByUsers===null?<NoComments/>:
                Comments.content.commentedByUsers.map(ncomments)}   
            </Base>
        </>
    )
}

export const Filter = ({click,display})=>{
    const [disp,setDisp] = useState("none")
    const show=()=>{
        setDisp("block")
    }
    const hide=()=>{
       setDisp("none")
   }
    return(
       
        <>
           <div className='dropdown' style={{float:"right", display:display}}>
               {disp=="none"?<i className="fa fa-filter dropbtn" onClick={show}></i>:<i className="fas fa-times dropbtn" style={{fontSize:'25px',paddingBottom:'8px'}} onClick={hide}></i>}
               <div className="dropdown-content" style={{display:disp}}>
                
                  <button className="btn btn-block" value="mostliked" onClick={click}>Most Liked</button>
                  <button className="btn btn-block" value="mostrecent" onClick={click}>Most Recent</button>
               </div>
           </div>
        </>
    )
}
export const Threedot = ()=>{
    const [disp,setDisp] = useState("none")
    const show=()=>{
        setDisp("block")
    }
    const hide=()=>{
       setDisp("none")
   }
    return(
        <>
           <div className='dropdown' style={{float:"right"}}>
               {disp=="none"?<i className="fa fa-ellipsis-v dropbtn" onClick={show}></i>:<i className="fas fa-times dropbtn" style={{fontSize:'25px',paddingBottom:'8px'}} onClick={hide}></i>}
               <div className="dropdown-content" style={{display:disp}}>
                   <a href="#">Report</a>
                  
               </div>
           </div>
        </>
    )
}

 const NoComments=()=>{
    return(
        <div className='comments'>
            <img src="https://commentphotos.com/gallery/CommentPhotos.com_1388005763.jpg"  alt="No Comments"/>
        </div>
    )
}

