import React, { useState, useEffect } from 'react';
import {Link, useParams} from 'react-router-dom'

import {Threedot} from './Components'
import {SetComment, SetLike} from './API/Api'
import {CommentBox,Like} from './Components'

export const Card=(props)=>{

    const [bringComment,setbringComment] = useState("none");
    
    const [OnComment, setOnComment] = useState("")

    const [CommentCount,setCommentCount]=useState(props.commentCount)

    const onchange=(event)=>{
        const add=event.target.value
       
          
            setOnComment(add)
        
        
    }

    const comment=()=>{
        setbringComment("block")
    }
    const send=()=>{
        setbringComment("none")
        if(OnComment.trim()!="")
        {
            setCommentCount(CommentCount+1)
            SetComment(OnComment,props.photo_id)
        }
        setOnComment("")
    }

    const link1=`/viewcomments/${props.photo_id}`
   
const image=props.Uploaded_photo



    return(
        <>
    <div className="cards bg-light">
        <div className="card container">
            <div className="card-header">
                <div class="d-flex bd-highlight ">
                    <div class=" bd-highlight">
                        
                        <img src={props.Profile_pic} alt="Avatar" className="profile-pic" />
                    </div>

                    <div className="profile-name bd-highlight dot-div">
                        <div className="text-dark bold profile-text">{props.name} </div>
                    </div>

                    <div class="ml-auto bd-highlight">
                        <Threedot></Threedot>
                    </div>
                </div>
            </div>
                {
                    <img src={image} alt='picture' className='card__img'/>
                }
                <div className="card__info">
                <div class="d-flex bd-highlight mb-2">
                    <div class=" bd-highlight">
                        <Like
                            like={props.likes}
                            photoId={props.photo_id}
                            likedByUsers={props.likedByUsers}
                            LikedArray={props.LikeArray}
                            userDetails={props.userId}
                            
                        > 
                        </Like>
                    </div>
                    <div class=" bd-highlight card-comment-icon">
                    <i class="far fa-comment-dots icon-size" onClick={comment}></i>
                    <div className="like">{CommentCount}</div>
                    </div>
                    
                   
                </div>
                <div className='description'>
                   
                    <div className="profile-caption" >{props.description} </div>
                    <div style={{display:bringComment}}>
                        <CommentBox send={send} val={OnComment} change={onchange}></CommentBox>
                    </div>
                   <div>
                       
                       <Link to={link1} >
                       <span>View All Comments</span>
                   </Link> 
                   </div>
                    
                </div>
                </div>
            </div>
    </div>
               
    </>
    )
}


export const CommentCard=(props)=>{
    return(
        <>
        
        
        <div className="card d-flex flex-row bd-highlight mb-3">
            <div class="p-2 bd-highlight">
                <img src={props.profile} alt="Avatar" className="comment-pic"/>
            </div>
            <div className="p-2 bd-highlight flex-fill comment-div">
                <div className="text-dark comment-name">
                    {props.name} 
                </div>
                <div className='comment-text'>
                    {props.comments}
                </div>
            </div>
            
        </div>
       
        </>
    )
}