import React, { useState, useEffect } from 'react';
import {Link} from 'react-router-dom';
import Base from './Base';
import {Card} from './Card';
import defaultpic from './defaultpic.png'


import {GetPhotoFromAPI,GetAllPhotoBasedOnFilter,GetUserDetails} from '../core/API/Api'
// import {DronaHQ} from './dronahq';
import {envr} from './API/Api';

// import './dronahq';

const Home=()=>{

  
   const [value, setValue] = useState({
       loader:true,
       content:[]
   })
   const [WhichFIlter, setWhichFIlter] = useState("")

   const [MostRecent, setMostRecent] = useState({
       loader:true,
       content:[]
   })
   const [MostLiked, setMostLiked] = useState({
    loader:true,
    content:[]
})
    const [UserDetails,setUserDetails]=useState({})

    // const DronaHQ = window.DronaHQ;

    useEffect(() => {
        
         /*******************************  EOF   ::  for testing  ****************************************/
        if(envr=='devp' &&  WhichFIlter==""){
            localStorage.setItem("useremail", "ghw@account.dronahq.com");
            localStorage.setItem("username", "GHW Admin");
            localStorage.setItem("usernonce", "db0a21604f");
            GetPhotoListing()
        }
        else
        {
            localStorage.setItem("useremail", "ghw@account.dronahq.com");
            localStorage.setItem("username", "GHW Admin");
            localStorage.setItem("usernonce", "db0a21604f");
            GetPhotoByFilter(WhichFIlter)
        }
        // else{
             /*****************************************  for Device Live ****************************************/
            // getProfile().then((data) => {
            //     // console.log(data);
            //     localStorage.setItem("useremail", data.email);
            //     localStorage.setItem("username", data.name);
            //     localStorage.setItem("usernonce", data.nonce);
        // })
        // }
    },[WhichFIlter])

    // useEffect(() => {
         
    //       if( WhichFIlter=="")
    //       {
        
    //             GetPhotoListing();
    //       }
    //     else
    //     {
            
    //             GetPhotoByFilter(WhichFIlter)
    //     }

    //     /********************************************************************************************************* */
        
    //       },[WhichFIlter])
          useEffect(()=>{
            userDetails()
        },[])

    const ncard=(val)=>{
        return(
            <>
        <Card 
            Profile_pic={val.uploaded_by_user_img!==""?val.uploaded_by_user_img:defaultpic} 
            name={val.uploaded_by_user_name} 
            likes={val.like_count}
            description={val.photo_caption}
            Uploaded_photo={val.photo_urls} 
            photo_id={val.photo_id}
            likedByUsers={val.likedByUsers}
            userId={UserDetails}
            commentCount={val.comment_count}
            // test1={test}
            
        >
        </Card>
            </>
        )
    }

    
   const userDetails= async()=>{
    const data=await GetUserDetails()
    setUserDetails(data)
   }

    const  GetPhotoListing= async ()=>{

        const data = await GetPhotoFromAPI()
        
        setValue({...value,content:data,loader:false})
        // GetPhotoFromAPI().then(response=>{
        //     const data=response.result.rows
        //     setValue({...value,content:data,loader:false})
        //   })
    }



    const GetPhotoByFilter= async(whichFilter)=>{
        let filterValue
        if(whichFilter.trim().toLowerCase()==='mostrecent')
        {
            filterValue="most-recent"
        }
        else{
            filterValue="most-liked"
        }
       const data=await GetAllPhotoBasedOnFilter(filterValue)
        
        if(whichFilter.trim().toLowerCase()==='mostrecent')
        {
            setMostRecent({...MostRecent,content:data,loader:false})
            setValue({...value,loader:false})
        }
        else 
        {
        
            setMostLiked({...MostLiked,content:data,loader:false})
            setValue({...value,loader:false})
        }
    
    }
    
    const click=(event)=>{
        setWhichFIlter(event.target.value)
    }

    const display=()=>{
        if(WhichFIlter==="")
        {
          return  value.loader===true?<img src="https://media.giphy.com/media/3oEjI6SIIHBdRxXI40/giphy.gif" class="loader"/>:value.content.map(ncard)
        }
        else if(WhichFIlter==="mostrecent")
        {
            return  MostRecent.loader===true?<img src="https://media.giphy.com/media/3oEjI6SIIHBdRxXI40/giphy.gif" class="loader"/>:value.content.map(ncard)
        }
        else
        {
            return  MostLiked.loader===true?<img src="https://media.giphy.com/media/3oEjI6SIIHBdRxXI40/giphy.gif" class="loader"/>:MostLiked.content.map(ncard)
        }
    }
    
    
    
    return(
        <>
             

        <Base funFilter={click} funNav='exit' disp="block">

        
        
            <div>
            {
              display()
              
            }
            
            
                    <Link to='/uploadphoto' class="float">
                        <i class="fa fa-plus my-float"></i>
                    </Link> 
            </div>
        </Base>
        </>
    );
}
export default Home